//search
export const querySearchShop = async(options) => {
    var res = await fetch('/datas/queryShopList.json', options)
    if(res.success){
        res.shopList = res.shops.map((shops) => ({
            shopId: shops.shopId,
            market: shops.market,
            shopNum: shops.shopNum,
            starNum: shops.starNum,
            telephone: shops.telephone,
            mainCase: shops.mainCase,
            itemNum: shops.itemNum,
            shopHeadUrl: shops.shopHeadUrl,
        }))
    }
    return res
}

