<template>
  	<div>
        <common-head :view="{
            goback: true,
            pageTitle: '',
            togglePageType: true,
        }"></common-head>
		
		<section class="goods_menu">
			<ul>
                <li v-for="item in catMenu" :class="{current: !keyword && curPid == item.pid || !item.pid && keyword == item.keyword}">
                    <span v-if="item.pid" @click="changePid(item.pid)">{{item.name}}</span>
                    <span v-else @click="changeKeyword(item.keyword)">{{item.name}}</span>
                </li>
			</ul>
		</section>
    
        <section class="goods-list"><goods-list :goods="goodsList"></goods-list></section>
        
        <div v-show="hasNext" @click="nextPage" class="next_page"><span></span></div>
        
    </div>
</template>

<script>
import {mapState} from 'vuex'
import commonHead from '../components/commonHead'
import goodsList from '../components/goodsImgList'
import {queryGoodsList2, ajax} from '../data/service'

export default {
    name: 'newgoods',
    components:{
    	commonHead,
        goodsList
    },
    data: function(){
    	return{
            categories: {
                30:  [
                    {pid:'30', name:''},
                    {pid:'50011740', name:''},
                    {keyword:'', name:''},
                    {pid:'50006842', name:''},
                    {pid:'50010404', name:''}
                ],
                16: [
                    {pid:'16', name:''},
                    {pid:'50006843', name:''},
                    {keyword:'', name:''},
                    {pid:'50006842', name:''},
                    {pid:'50010404', name:''}
                ],
            },
            catMenu: [],
            curPid: 30,
            keyword: '',
            goodsList: [],
            pageIndex: 1,
            hasNext: false,
            rerender: false,
    	}
    },
    created(){
        this.initCatMenu()
        this.queryGoodsList()
        
    },
    mixins: [ajax],
    methods:{
        initCatMenu(){
            this.catMenu = this.categories[this.curPid = this.pid]
        },
        async queryGoodsList(){
            this.ajax(await queryGoodsList2(this.curPid, this.pageIndex, this.keyword), (res) => {
                if(this.rerender){
                    this.rerender = false
                    this.goodsList = res.goodsList
                }else{
                    this.goodsList = this.goodsList.concat(res.goodsList)
                }
                this.hasNext = res.hasNext
            })
        },
        nextPage(){
            this.pageIndex++
            this.queryGoodsList()
        },
        changePid(pid){
            if(pid != this.curPid){
                this.keyword = ''
                this.curPid = pid
                this.pageIndex = 1
                this.rerender = true
                this.queryGoodsList()
            }
        },
        changeKeyword(keyword){
            if(keyword != this.keyword){
                this.keyword = keyword
                this.curPid = this.pid
                this.pageIndex = 1
                this.rerender = true
                this.queryGoodsList()
            }
        }
    },
    computed: {
        ...mapState(['pid']),
    },
    watch: {
        pid(){
            this.changePid(this.pid)
            this.initCatMenu()
        },
    }
}
</script>

<style scoped>
.goods_menu{width:100%; height:.48rem; padding-top:.04rem; background:#fff; position:fixed; top:.46rem; left:0; box-shadow:0 1px .06rem rgba(0,0,0,.1);}
.goods_menu ul{display:flex; text-align:center;}
.goods_menu li{flex:1; font-size:.15rem;}
.goods_menu li span{display:inline-block; padding:0 .08rem; height:.44rem; line-height:.44rem; border-bottom:.02rem solid #fff;}
.goods_menu li.current span{border-color:#333;}

.goods-list{margin-top:.08rem; padding-top:.92rem; overflow:hidden;}
</style> 




