<template>
	<div>
		<common-head :view="{
			goback: true,
			pageTitle: ''
		}"></common-head>

		<ul class="setting_list">
			<li class="head_img">
				<label></label>				
				<i class="iconfont icon-right"></i>
				<span>
					<img :src="headUrl ? headUrl : '../../static/headUrl.jpg'">
					<input type="file" accept="image/*">
				</span>
			</li>
			<li>
				<label></label>
				<span>{{telephone}}</span>
			</li>
			<li>
				<router-link to="/modifyPassword">
					<label></label>				
					<i class="iconfont icon-right"></i>
					<span>********</span>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
import commonHead from '../components/commonHead'
import {getUserInfo, ajax} from '../data/service'
export default{
	data(){
		return{
			headUrl: '',
			telephone: ''
		}
	},
	created(){
		this.getUserBase()
	},
	mixins:[ajax],
	methods:{
		async getUserBase(){
			this.ajax(await getUserInfo(), function(resp){
				this.headUrl = resp.headUrl
				this.telephone = resp.phoneBind
			})
		}
	},
	components:{
		commonHead
	}
}
</script>

<style scoped>
.setting_list{padding-top:.48rem;}
.setting_list li{margin-top:.1rem; background:#fff; font-size:.16rem; color:#666; padding:.12rem .16rem; overflow:hidden;}
.setting_list li label{width:;}
.setting_list li span{float:right; margin-right:.12rem; margin-top:.03rem;}
.setting_list li i{float:right; color:#cdced2; margin-top:.04rem;}
.setting_list li a{color:#666;}
.setting_list li.head_img label{display:inline-block; padding-top:.3rem;}
.setting_list li.head_img span{position:relative; margin-top:0; display:inline-block; width:.78rem; height:.78rem; overflow:hidden;}
.setting_list li.head_img span input{width:.78rem; height:.78rem; background:#fff; position:absolute; top:0; left:0; opacity:0;}
.setting_list li.head_img img{width:.78rem; height:.78rem; border-radius:50% 50%;}
.setting_list li.head_img i{margin-top:.32rem;}
</style>


